.detailmenu1 {
    width: 290px;
    height: 110px;
    background-image: linear-gradient(rgba(231,247,254, .9), rgba(255,255,255, .9), rgba(216,228,234, .9));
	box-shadow: 2px 2px 5px #3d3c3c;
    position: fixed;
    border-radius: 10px;
}
.detailmenu1 .title {
    margin-top: 4px;
    margin-left: 10px;
    color: rgb(75,97,111);
    font-family: 'Times New Roman';
    font-weight: bold;
    font-size: 13px;
}
.detailmenu1 .right-title-btn {
    float: right;
    width: 13px;
    height: 13px;
    cursor: pointer;
    box-sizing: border-box;
    filter:drop-shadow(0px 1px 2px grey)
}
.detailmenu1 .right-title-btn:before {
    content: "";
    width: 2px;
    height: 13px;
    background: #fff;
    transform: rotate(45deg);
    top: -17px;
    right: 11px;
    position: absolute;
}
.detailmenu1 .right-title-btn:after {
    content: "";
    width: 2px;
    height: 13px;
    background: #fff;
    transform: rotate(-45deg);
    top: -17px;
    right: 11px;
    position: absolute;
}
.detailmenu1 .itemlist {
    height: 70px;
}
.detailmenu1 .itemlist .itemLeft {
    float: left;
    margin-top: 25px;
    margin-left: 2px;
    cursor: pointer;
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-right: 12px solid white;
    border-bottom: 10px solid transparent;
    filter:drop-shadow(0px 1px 2px grey)
}
.detailmenu1 .itemlist .itemRight {
    float: right;
    cursor: pointer;
    margin-top: 25px;
    margin-right: -8px;
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-left: 12px solid white;
    border-bottom: 10px solid transparent;
    filter:drop-shadow(0px 1px 2px grey)
}
.detailmenu1 .itemlist img {
    float: left;
    margin-left: 10px;
    margin-top: 10px;
    width: 60px;
    height: 60px;
    filter:drop-shadow(0px 1px 2px grey)
    
}
.detailmenu1 .itemlist .item{
    float: left;
    margin-left: 2px;
    margin-top: 28.5px;
    color: rgb(75,97,111);
    font-family: 'Times New Roman';
    font-weight: bold;
    font-size: 13px;
}
.detailmenu1 .itemlist .item a {
    color:rgb(59,146,188);
}
.detailmenu1 .footer img{
    float: left;
    margin-top: 2px;
    margin-left: 30px;
}
.detailmenu1 .footer {
    color: rgb(198,106,142);
    font-size: 10px;
}
.detailmenu1 .footer a{
    color: rgb(198,106,142);
}
