
  .profile {
    width: 167px;
    height: 234px;
    left: 0px;
    top: 0px;
    position: fixed;
    background: url('../../../assets/PetStatus/ditu01.png') 0px 0px no-repeat,
      url('../../../assets/PetStatus/ditu02.png') 42px 0px no-repeat,
      url('../../../assets/PetStatus/ditu03.png') 84px 0px no-repeat,
      url('../../../assets/PetStatus/ditu04.png') 0px 101px no-repeat,
      url('../../../assets/PetStatus/ditu05.png') 32px 101px no-repeat,
      url('../../../assets/PetStatus/ditu05.png') 64px 101px no-repeat,
      url('../../../assets/PetStatus/ditu05.png') 96px 101px no-repeat,
      url('../../../assets/PetStatus/ditu05.png') 105px 101px no-repeat,
      url('../../../assets/PetStatus/ditu06.png') 135px 101px no-repeat,
      url('../../../assets/PetStatus/ditu07.png') 0px 139px no-repeat,
      url('../../../assets/PetStatus/ditu08.png') 32px 139px no-repeat,
      url('../../../assets/PetStatus/ditu08.png') 64px 139px no-repeat,
      url('../../../assets/PetStatus/ditu08.png') 96px 139px no-repeat,
      url('../../../assets/PetStatus/ditu08.png') 105px 139px no-repeat,
      url('../../../assets/PetStatus/ditu09.png') 135px 139px no-repeat;
  }
  .profile .head {
    width: 80px;
    height: 22px;
    background: url('../../../assets/PetStatus/00.png') 8px 5px no-repeat;
    float: left;
    cursor: pointer;
  }
  .profile .head:active {
    opacity: 0.7;
  }
  .profile .head_fresh:active {
    opacity: 0.7;
  }
  .profile .head_fresh {
    cursor: pointer;
    width: 42px;
    height: 22px;
    background: url('../../../assets/PetStatus/shuaxin00.png') 2px 6px no-repeat;
    float: right;
  }
  .profile .info {
    width: 140px;
    height: 167px;
    margin-left: 14px;
    margin-top: 6px;
    display: inline-block;
    font-size: 10px;
    color: rgb(90, 84, 67);
    font-family: 'Times New Roman';
    font-weight: bold;
  }
  .item div {
    display: inline-block;
  }
  .profile .footer_left:active {
    opacity: 0.7;
  }
  .profile .footer_left {
    cursor: pointer;
    width: 85px;
    height: 20px;
    background: url('../../../assets/PetStatus/tie.png') 15px 2px no-repeat;
    float: left;
  }
  .profile .footer_right1:active {
    opacity: 0.7;
  }
  .profile .footer_right2:active {
    opacity: 0.7;
  }
  .profile .footer_right1 {
    cursor: pointer;
    width: 37px;
    height: 20px;
    background: url('../../../assets/PetStatus/kaiqi_up.png') 2px -2px no-repeat;
    float: right;
  }
  .profile .footer_right2 {
    cursor: pointer;
    width: 42px;
    height: 20px;
    background: url('../../../assets/PetStatus/guanbi_down.png') 0px -2px no-repeat;
    float: right;
  }
  .basic_bar {
    position:absolute;
    width: 100px;
    z-index: -1;
    background: url('../../../assets/PetStatus/jindutiao2.png') 0px 0px repeat;
    border-radius: 10px;
  }
  .bar_value {
    position:absolute;
    z-index: 0;
    background: url('../../../assets/PetStatus/lusejindutiao01.png') 0px 0px repeat;
    border-radius: 10px;
  }
  .bar_value:hover {
    cursor: pointer;
  }
  .bar_value1 {
    position:absolute;
    z-index: 0;
    background: url('../../../assets/PetStatus/bluejidutiao_02.png') 0px 0px repeat;
    border-radius: 10px;
  }
  .bar_value1:hover {
    cursor: pointer;
  }
  .bone {
    width: 12px;
    height: 12px;
    margin-left: -3px;
    margin-bottom: -2px;
    background: url('../../../assets/PetStatus/dengji.png') 0px 0px no-repeat;
  }