
.contextMenu {
    position: fixed;
}
.contextMenu a{
    text-decoration:none;
}
.contextMenu .option{
	cursor: pointer;
	color:rgb(75,75,52);
}
.contextMenu .option:hover{
	color: pink;
}
.contextMenu-option {
	height: 19px;
	background-image: url('../../../assets/PetMenu/ditu05_2.bmp');
	background-repeat: repeat;
	float:left;
}
