    font-size: 10px;
}
.detailmenu3 .right-title-btn {
    float: right;
    width: 13px;
    height: 13px;
    cursor: pointer;
    box-sizing: border-box;
    -webkit-filter:drop-shadow(0px 1px 2px grey);
            filter:drop-shadow(0px 1px 2px grey)
}
.detailmenu3 .right-title-btn:before {
    content: "";
    width: 2px;
    height: 13px;
    background: #fff;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 0px;
    right: 11px;
    position: absolute;
}
.detailmenu3 .right-title-btn:after {
    content: "";
    width: 2px;
    height: 13px;
    background: #fff;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    top: 0px;
    right: 11px;
    position: absolute;
}
.detailmenu3 .study_detail {
    float: right;
    margin-top: 0px;
